			'subtitle': 'Soloist',
			'description': 'Yuja Wang is one of the world’s most sought-after pianists, performing with top orchestras all around the world in her 2016-2017 season. Born in Beijing, Wang began playing the piano at 6 years old and one year later, she was a student at the Central Conservatory of Music in Beijing. In 2001, she was named a Steinway Artist and by 2002 she was studying with Gary Graffman at the Curtis Institute of Music in Philadelphia. Among her many accolades is the 2006 Gilmore Young Artist Award, the 2009 Gramophone Classic FM’s Young Artist of the Year Award, the 2011 Echo Klassik Young Artist of the Year Award and, most recently, being named Musical America’s Artist of the Year in December of 2016. Her performance of Nikolai Rimsky-Korsakov’s “Flight of the Bumblebee” at the Verbier Festival has been viewed on YouTube more than 4.5 million times. Her impeccable technique, intelligent musical interpretations, and star power make Wang an inspiration to young musicians not only in China, but around the world.'
