import React from 'react'
import cx from 'classnames'

import './instrument.scss'
import './font.css'

export default class Instrument extends React.Component {
	constructor(props) {
		super(props)
		this.handleClick = this.handleClick.bind(this)	
	}
	handleClick() {
		this.props.onClick && this.props.onClick()
	}
	render() {
		var classes = cx('instrument', {'instrument_active': this.props.active}, {'instrument_inversed': this.props.inversed}),
			iconClasses = 'instruments-icon instruments-icon-' + this.props.icon

		return (
			<li className={classes} onClick={this.handleClick}>
				<div className="instrument__content">
					<div className="instrument__icon">
						<i className={iconClasses}></i>
					</div>
					<div className="instrument__title">{this.props.title}</div>
				</div>
			</li>
		)
	}
}


