import React from 'react'
import cx from 'classnames'
import './big-icons.scss'

export class BigIcons extends React.Component {
	render() {
		var className = cx('big-icons', {'big-icons_full': this.props.full})
		return <div className={className}>{this.props.children}</div>
	}
}

export class BigIcon extends React.Component {
	render() {
		var iconClassName = cx('icon', 'icon-' + this.props.icon),
			titleClassName = cx('big-icon__title', {'big-icon__title_gray': this.props.grayTitle})
		return (
			<div className="big-icon">
				<div className="big-icon__circle">
					<i className={iconClassName}></i>
				</div>
				<div className={titleClassName}>{this.props.children}</div>
			</div>
		)
	}
}


