import React from 'react'
import locale from 'locale'
import {BigIcons, BigIcon} from '../BigIcons'

import './tuition-free.scss'

class TuitionFree extends React.Component {
	shouldComponentUpdate() {
		return false
	}
	render() {
		return (
			<div className="tuition-free">
				<h2 className="middle-title">{locale.str('about.tuition_free.title')}</h2>
				<div className="middle-subtitle">{locale.str('about.tuition_free.subtitle')}</div>
				<BigIcons full>
					<BigIcon icon="education">{locale.str('about.tuition_free.icons.education')}</BigIcon>
					<BigIcon icon="travel">{locale.str('about.tuition_free.icons.travel')}</BigIcon>
					<BigIcon icon="housing">{locale.str('about.tuition_free.icons.housing')}</BigIcon>
				</BigIcons>
				<p className="gray-text">{locale.str('about.tuition_free.description')}</p>
			</div>
		)
	}
}

export default TuitionFree


