import React from 'react'
import './the-map.scss'

const MAPBOX_ACCESS_TOKEN = 'pk.eyJ1Ijoic2xhdmFib2dvdiIsImEiOiJlOTMyYmJhYjNjNDEyOWYwN2VmNzYxYTIzOTAxNjVkNiJ9.IazzXwqlV_-TYwgejcz20w'
const MAPBOX_STYLE_URL = 'mapbox://styles/slavabogov/ciqw2kqfs0009c7mjhqfzy5a0'
const MAPBOX_BOUNDS_PADDING = 100

export default class extends React.Component {
	componentDidMount() {
		mapboxgl.accessToken = MAPBOX_ACCESS_TOKEN;
		
		var map = new mapboxgl.Map({
		    container: 'map-tooltips-js',
		    style: MAPBOX_STYLE_URL
		})

		map.scrollZoom.disable();

	  	var geojson = this.props.points.map(markerData => {
	  		return {
				type: 'Feature',
				geometry: {
					type: 'Point',
				  	coordinates: markerData.coordinates
				},
				properties: {
				  	city: markerData.city,
				  	place: markerData.place
				}
		  	}
	  	})

	  	// Create a popup, but don't add it to the map yet.
		var popup = new mapboxgl.Popup({
		    closeButton: false,
		    closeOnClick: false
		})

		map.on('mousemove', function(event) {
		    var features = map.queryRenderedFeatures(event.point, { layers: ['points'] });
		    // Change the cursor style as a UI indicator.
		    map.getCanvas().style.cursor = features.length ? 'pointer' : '';

		    if (!features.length) {
		        popup.remove();
		        return;
		    }

		    var feature = features[0];

		    // Populate the popup and set its coordinates
		    // based on the feature found.
		    popup.setLngLat(feature.geometry.coordinates)
		        .setHTML('<div class="athena-map-popup__city">'+ feature.properties.place+'</div>' + '<div class="athena-map-popup__place">'+ feature.properties.city+'</div>')
		        .addTo(map);
		});


		map.on('load', function() {
		    var bounds = new mapboxgl.LngLatBounds();

		    map.addSource('points', {
		        type: 'geojson',
		        data: {
		            type: 'FeatureCollection',
		            features: geojson
		        }
		    })

		    map.addLayer({
		        id: 'points',
		        type: 'symbol',
		        source: 'points',
		        layout: {
		            'icon-image': 'pin',
		            'icon-allow-overlap': true,
		            'icon-size': 0.075
		        }
		    })

			geojson.forEach(function(feature) {
			    bounds.extend(feature.geometry.coordinates);
			})

			map.fitBounds(bounds, {
				padding: MAPBOX_BOUNDS_PADDING
			});
		});
	}

	render() {
		return <div className="map" id="map-tooltips-js"></div>
	}
}


