import React, { Component, PropTypes } from 'react'
import locale from 'locale'
import ArtistCard from '../ArtistCard'

class Artists extends Component {
	static artistCardMarkup(artist, index) {
		let align = index % 2 === 0 ? 'left' : 'right';
		return (
			<ArtistCard
				image={artist.photo}
				title={artist.title}
				subtitle={artist.subtitle}
				description={artist.description}
				align={align}
				key={index}
			/>
		)
	}

	render() {
		let artists = this.props.artists.map(Artists.artistCardMarkup)

		return (
			<div>
				<div className="wrapper-1000px wrapper-padding-top wrapper-padding-bottom">
					<div className="gray-text">{locale.str('artists.description')}</div>
				</div>
				<hr className="short"/>
				<div className="wrapper-1000px wrapper-padding-top wrapper-padding-bottom">{artists}</div>
			</div>
		)
	}
}

Artists.propTypes = {
	artists: PropTypes.arrayOf(PropTypes.shape({
		photo: PropTypes.string,
		title: PropTypes.string,
		subtitle: PropTypes.string,
		description: PropTypes.string
	})).isRequired
}

export default Artists


