import React from 'react'
import cx from 'classnames'
import './instruments-block.scss'

export default class InstrumentsBlock extends React.Component {
	render() {
		var classes = cx('instruments-block', {'instruments-block_gray': this.props.gray}),
			iconClasses = 'icon icon-' + this.props.icon

		return (
			<div className={classes} id={this.props.id}>
				<div className="wrapper-1000px">
					<ul className="instruments-items">
						{this.props.children}
					</ul>
				</div>
			</div>
		)
	}
}


