import React from 'react'
import SlickSlider from 'react-slick';
import {LeftSliderButton, RightSliderButton} from '../SliderArrows/'
import './sponsors.scss'

const SPONSORS_SLIDER_SETTINGS = {
    dots: true,
    infinite: true,
    speed: 1000,
    autoplay: true,
    autoplaySpeed: 10000,
    slidesToShow: 3,
    slidesToScroll: 3,
    pauseOnHover: true,
    nextArrow: <RightSliderButton/>,
    prevArrow: <LeftSliderButton/>,
    className: 'sponsors-slider',
    dotsClass: 'slider__dots',
    responsive: [
        {
            breakpoint: 768,
            settings: {
                speed: 300,
                autoplay: false,
                arrows: false,
                slidesToShow: 2,
                slidesToScroll: 2,
            }
        }
    ]
}

export default class Sponsors extends React.Component {
	render() {
		return (
			<div className="sponsors">
				<h3 className="slider__title">Sponsors</h3>
				<div className="wrapper-1000px">
					<SponsorsSlider data={this.props.sponsors}/>
				</div>
			</div>
		)
	}
}

const SponsorsSlider = React.createClass({
	
    render() {
        let columns = [],
            images = this.props.data

        for (let i = 0; i < images.length; i += 2) {
            columns.push(generateSponsorsColumn(i, images[i], images[i+1]))
        }

    	return (
      		<SlickSlider {...SPONSORS_SLIDER_SETTINGS}>
        		{columns}
     		</SlickSlider>
    	);
  	}
});


/**
     * Generate column with images
     * @private
     * @param  {Number} index   Unique key of column for React
     * @param  {String} images  URLs of images
     * @return {Node}           The column div with class 'sponsors-column'
     */
function generateSponsorsColumn(index) {
    let images = []
    for (let i = 1; i < arguments.length; i++) {
        let imageData = arguments[i]
        images.push(<div className="sponsors-item" key={imageData.id} style={{backgroundImage: `url(${imageData.image})`}}></div>)
    }
    return <div className="sponsors-column" key={index}>{images}</div>
}


