import React from 'react'
import Textarea from 'react-textarea-autosize'
import cx from 'classnames'
import './contact.scss'

import {ContactIcons, ContactIcon} from '../ContactIcons/'

const SUCCESS_TEXT_SHOW_TIMEOUT = 3000

export default class extends React.Component {
	state = {
		isSubmitted: false
	}

	handleSubmit(event) {
		event.preventDefault();
		let form = event.target,
			name = form.name.value,
			email = form.email.value,
			message = form.message.value;

		if (name === '' || email === '' || message === '') {
			return
		}

		fetch('http://w-conservatory.herokuapp.com/inquiries/message', {
			method: 'POST',
			body: new FormData(form)
		}).then(() => {
			this.setState({isSubmitted: true}, () => {
				setTimeout(() => {
					form.reset();
					this.setState({isSubmitted: false})
				}, SUCCESS_TEXT_SHOW_TIMEOUT)
			})
		}).catch(error => {
			alert('Error occurred')
		})
	}
	render() {
		let inputsClassName = cx('contact-us__inputs', {'contact-us__inputs_hidden': this.state.isSubmitted})
		let successTextClassName = cx('contact-us__submitted', {'contact-us__submitted_hidden': !this.state.isSubmitted})

		return (
		    <div className="contact-us" id="contact">
		    	<div className="wrapper-1000px wrapper-padding-top wrapper-padding-bottom">
		    		<div className="contact-us__title">Contact</div>
		    		<ContactIcons white>
		    			<ContactIcon icon="phone">+1 (646) 833-7292 (English)<br/>+1 (646) 833-7290 (Chinese)</ContactIcon>
		    			<ContactIcon icon="location">CitySpire Center<br/>156 West 56th Street, Suite 702<br/>New York, New York 10019</ContactIcon>
		    			<ContactIcon icon="mail">english@nyochina.org<br/>chinese@nyochina.org</ContactIcon>
		    		</ContactIcons>
		    		<form className="contact-us__form" onSubmit={this.handleSubmit.bind(this)}>
		    			<span className={successTextClassName}>Your message has been successfully sent!</span>
		    			<span className={inputsClassName}>
							<input className="contact-us__form-input" placeholder="Name" type="text" name="name" autoComplete="off" required/>
							<input className="contact-us__form-input" placeholder="Email" type="email" name="email" autoComplete="off" required/>
							<Textarea className="contact-us__form-textarea" placeholder="Message" name="message" maxRows={5} autoComplete="off" required></Textarea>
						</span>
						<button className="contact-us__form-submit" disabled={this.state.isSubmitted}>Send</button>
		            </form>
				</div>
		    </div>
		)
	}
}


