		'headlining_artists': [{
			'photo': 'img/yuja-wang.jpg',
			'title': '王羽佳',
			'subtitle': '独奏家',
			'description': '王羽佳，当今世界最抢手的钢琴家之一，2016-2017季度已被世界各大顶级交响乐团预订，将在世界各地进行演出。出生于北京的王羽佳自6岁起学习钢琴，一年后便进入中央音乐学院附中进修。2001年，她被提名为施坦威钢琴独家艺术家(Steinway Artist)；2002年，她被费城柯蒂斯音乐学院录取，跟随大师加里·格拉夫曼(Gary Graffman)学习。她曾荣获多项艺术大奖，其中包括2006年吉尔莫年轻艺术家奖(Gilmore Young Artist Award)；2009年古典音乐留声机杂志(Gramophone Classic FM) 年度“青年艺术家”奖；2011年古典回声奖(Echo Klassik)年度“青年艺术家”奖；2016年音乐美国杂志(Musical America)“年度艺术家”。她在韦尔比耶艺术节(Verbier Festival)演奏尼古拉·里姆斯基-科萨科夫的《野蜂飞舞》在Youtube点击量超过四百五十万。她无可挑剔的演奏技术，对音乐深刻的洞察力和大气磅礴的演奏，让她成为中国乃至全世界青年音乐家的优秀榜样。'
		}, {
			'photo': 'img/ludovic-morlot.jpg',
			'title': 'Ludovic Morlot',
			'subtitle': '客座指挥家',
			'description': '路多维克·莫洛，现任西雅图交响乐团首席指挥，同时担任波士顿、纽约、芝加哥、洛杉矶等地交响乐团的常任客座指挥。路多维克·莫洛是新一代指挥家中冉冉升起的一颗璀璨之星。2002年至2004年期间，他曾担任里昂国家交响乐团(Orchestre National de Lyon, ONL)常任指挥, 并同时监管两支ONL青少年交响乐团。2004年至2007年期间，他担任波士顿交响乐团的助理指挥。他的音乐成就令人瞩目：曾在2002年坦格伍德音乐节(Tanglewood Music Festival)上被授予“小泽征尔奖”；2004年被引荐进入英国皇家音乐学院。在西雅图交响乐团任职期间，路多维克·莫洛还受聘担任华盛顿大学管弦乐指挥系主任。作为NYO-China的客座指挥，他将担任乐团彩排、卡内基音乐厅首演及中国巡演音乐会指挥。路多维克·莫洛常年致力于培养和扶持潜力出众的青少年音乐人才，他的加入必将给学员们带来与世界一流音乐家合作学习的绝佳机会。'
		}],
