		'hero.third_row': '2017年7月卡内基大厅首度与您见面',
	
		'endorsements.title': '名人支持',
	
		'about.mission': '使命：为中国杰出的青少年音乐家提供世界级的音乐指导和演出机会，进而培养他们成为世界舞台上促进古典音乐与中华文化交流的使者。',
		'about.first_paragraph': '首届中华青少年交响乐团 (NYO-China) 将于2017年夏在纽约卡内基大厅首度登台亮相，以此开启随后中美两国巡回演出。7月，逾百名初、高中年龄段的音乐学生将齐聚美国东北部一所大学校园，在世界级导师团队及知名指挥家路多维克·莫洛的指导下，参加为期两周的集训项目。经过两周的筹备训练，乐团将在纽约首次登台演出，钢琴家王羽佳将担任首场演出独奏家。随后乐团将开启在中国北京、上海、广州三地的巡回演出。',
		'about.second_paragraph': '深受卡内基音乐厅美国国家青年交响乐团 (NYO-USA)启发，NYO-China参考其运作模式，为中国青少年音乐家量身打造顶级音乐教育项目，为他们提供世界一流的音乐教育资源和演出机会。经过竞争激烈的线上海选，脱颖而出的学员将师从当今最优秀的音乐家，在世界级的舞台上展示交响乐魅力，成为国际舞台上古典音乐与中华文化交流的使者。值得一提的是，NYO-China将承担所有正式学员的相关项目费用，包括学费、旅费和住宿费等，确保勤奋而有天赋的青少年音乐家不会因为经费问题而错失展示才华的机会。',
	
		'about.tuition_free.title': '项目免费',
		'about.tuition_free.subtitle': '音乐教育资源人人共享',
		'about.tuition_free.icons.education': '教育',
		'about.tuition_free.icons.travel': '交通',
		'about.tuition_free.icons.housing': '住宿',
		'about.tuition_free.description': '各地都有杰出的艺术人才藏龙卧虎。我们希望每一位勤奋、有天赋的青少年音乐家都有平等的机会加入NYO-China。得益于赞助商的大力支持，NYO-China项目将承担正式学员在项目期间的所有相关费用，包括学费、旅费和住宿费。所有被录取的学员都不会因为经费问题而错失这个绝佳机会。',
	
		'about.timelime.audition_process': 'Audition Process',
		'about.timelime.training_residency': '住宿制训练营',
		'about.timelime.concert_tour': '巡回演出',
	
		'artists.description': '成为NYO-China的一员，意味着将获得与世界级音乐家合作的机会。乐团的启动得到了世界各地诸多艺术家的积极响应。现任西雅图交响乐团首席指挥路多维克·莫洛将担任客座指挥家，世界知名钢琴家王羽佳将以独奏家身份加入演出。作为古典音乐领域的顶尖人才，路多维克大师和王羽佳等艺术家长期不遗余力地支持音乐教育事业，并且表达了通过本项目进一步培养中国新生代交响乐人才的热切期盼。他们是让NYO-China学员有幸成为优秀交响乐表演人才的不二人选。',
	
		'training_residency.description': 'NYO-China 住宿制训练营旨在让每一位成员都能在世界顶级音乐家的指导下追寻自己的音乐梦想。在为期两周的训练中，学员将居住在纽约近郊西康涅狄格州立大学校园中，参与一系列音乐强化训练课程及相关课外活动，以提高自身音乐素养及演奏技能，并为乐团的正式演出做各项排练准备。集训期间，学员将在客座指挥和音乐总监的带领下进行集体排练与分部排练，同时参加音乐讲习大师班以及各类音乐主题互动工坊。此外，学员们还有机会在纽约进行文化探索之旅，参加与卡内基音乐厅NYO-USA的音乐交流活动。',
		'training_residency.teaching_artists.title': '音乐导师',
		'training_residency.teaching_artists.description': '集训期间，学员将会在音乐总监蔡金东和客座指挥家路多维克·莫洛指导下进行大量乐团排练。同时，来自美国顶尖音乐学院的专业老师和知名交响乐团的首席演奏家们，将负责各乐器声部的分部排练及合成。这个顶尖的导师团队将竭尽所能，与每一位学员分享他们丰富宝贵的专业经验，打开一扇通往古典音乐世界的神奇之门。',
		'training_residency.teaching_artists.release_date': '音乐导师名单将在2016年11月18日公布',
	
		'concert_tour.description': '不同于其他的青少年交响乐团项目， NYO-China的巡演对于中国音乐学生而言将是独一无二的。2017年夏季的巡演将于7月22日在纽约卡内基音乐大厅隆重开启。美国首秀后，乐团将返回中国并在北京、上海、广州三地进行巡演。除了在这三座城市的三大剧院里进行表演外，乐团成员还将参观每个城市的名胜古迹，通过参加系列活动分享自己的心路历程。两国巡演作为NYO-China的最大亮点之一，为青少年音乐家们创造无比珍贵的机会使其在国际舞台上展示自己的音乐才华，从而成为推广古典音乐与本国文化的交流使者。',
	
		'auditions.description': '申请人通过网上平台提交自己的资料后进入线上海选环节。所有申请人都必须提交个人简历、个人陈述、指导老师的推荐信(中文或英文)以及一段指定乐器演奏视频。虽然集训营的导师和大部分员工都有双语交流能力，我们还是希望申请人能具备基本的英文交流能力。因此，为了提前了解申请人的英语表达能力，我们同时要求申请人录制并上传一段英文自我简介视频。所有来自中国大陆地区，年龄在14岁至18岁之间的青少年学生都有资格申请参加。申请将于2016年12月1日开放，与2017年2月28日关闭。',
		'auditions.general_requirements': '基本要求',
		'auditions.icons.resume': '上传简历',
