		'footer.description': '中华青少年交响乐团由纽约和北京两地专业团队共同运作。项目的初衷是为了让中国有潜力的青少年音乐家获得独一无二的演出平台和专业指导机会，让他们向家乡和世界展示自己出众的才华和努力的成果。',
	
		'common.about': '关于NYO-CHINA',
