		'hero.second_row': 'The First Annual Summer Concert Tour',
		'hero.third_row': 'Debuting in Carnegie Hall in July 2017',
	
		'endorsements.title': 'Endorsements',
	
		'about.mission': 'Mission: To provide China’s finest young musicians access to superior performance and training opportunities and to empower them to serve as ambassadors for classical music and their country on a world stage.',
		'about.first_paragraph': 'The first annual National Youth Orchestra of China is proud to present an international concert tour of the United States and China during the summer of 2017, inaugurated by a gala performance in Carnegie Hall. For two weeks in July, approximately one hundred high school-aged musicians will gather on a university campus in the Northeast United States to participate in an intensive two-week training program under the direction of a group of world-class teaching artists and a renowned guest conductor, Ludovic Morlot. After two weeks of preparation, the orchestra will debut in New York City and thereafter embark on a three-city concert tour of China featuring a soloist who will resonate with Chinese and Western audiences alike.',
		'about.second_paragraph': 'Inspired by Carnegie Hall’s own National Youth Orchestra of the United States of America (NYO-USA), NYO-China is meant to provide Chinese musicians access to the world-class education and performance model developed by its American counterpart. Members will have the chance to train under some of the finest living classical musicians and then perform as an orchestra on a world stage. After applying through a competitive virtual audition process, accepted members will be given the chance to serve as ambassadors for both classical music and their country. Moreover, to ensure that no superior musician will ever be turned away for lack of funds, the entire program – including housing, tuition, and travel – will be free of charge.',
	
		'about.tuition_free.title': 'Tuition Free',
		'about.tuition_free.subtitle': 'Ensuring music education opportunities for everyone.',
