import React from 'react'
import { Link } from 'react-router'
import './breadcrumbs.scss'

/*
	Breadcrumbs component
	Require @items - array of objects like:
	{
		title: "Some title",
		url: "/some url"
	}
	The last item in array may not contain url param cause it's not clickable link
*/
export default class Breadcrumbs extends React.Component {
	render() {
		let items = this.props.items,
			itemsLength = items.length,
			markupItems = []

		items.forEach((item, index) => {

			if (index === itemsLength - 1) {
				markupItems.push(
					<span className="breadcrumbs__current" key={index}>
						<i className="icon icon-breadcrumbs-arrow"></i>
						<span key={item.title} className="breadcrumbs-link breadcrumbs-link_active">{item.title}</span>
					</span>
				)
			} else if (index === itemsLength - 2) {
				markupItems.push(<Link key={item.title} to={item.url} className="breadcrumbs-link">{item.title}</Link>)
			} else {
				markupItems.push(<Link key={item.title} to={item.url} className="breadcrumbs-link">{item.title}</Link>)
				markupItems.push(<i key={index} className="icon icon-breadcrumbs-arrow"></i>)
			}

		})
		return <div className="breadcrumbs">{markupItems}</div>
	}
}

Breadcrumbs.defaultProps = {
	items: []
}


