import React from 'react'
import SlickSlider from 'react-slick';
import NewsroomCard from '../NewsroomCard'
import {LeftSliderButton, RightSliderButton} from '../SliderArrows'
import './related-news.scss'

const RELATED_NEWS_SLIDER_SETTINGS = {
	dots: true,
	infinite: true,
	speed: 1000,
	autoplay: true,
	autoplaySpeed: 10000,
	slidesToShow: 3,
	slidesToScroll: 3,
	pauseOnHover: true,
	nextArrow: <RightSliderButton/>,
	prevArrow: <LeftSliderButton/>,
	className: 'related-news-slider',
	dotsClass: 'slider__dots',
	responsive: [
		{
			breakpoint: 768,
			settings: {
				slidesToShow: 2,
				slidesToScroll: 2,
				speed: 300,
				arrows: false,
			}
		},
		{
			breakpoint: 480,
			settings: {
				slidesToShow: 1,
				slidesToScroll: 1,
				speed: 300,
				arrows: false,
			}
		},
	]
}

export default class RelatedNews extends React.Component {
	render() {
		return (
			<div className="related-news">
				<h3 className="middle-title">You May Also Like</h3>
				<div className="wrapper-1000px">
					<RelatedNewsSlider data={this.props.data}/>
				</div>
			</div>
		)
	}
}

function relatedNewsSliderPage(data, index) {
	return (
		<span className="related-news-slide" key={data.id}>
			<NewsroomCard
						id={data.id}
						image={data.preview}
						title={data.title}
						subtitle={data.subtitle}
						date={data.date}
						key={data.id}/>
		</span>
	)
}

const RelatedNewsSlider = React.createClass({
	render() {
		return (
			<SlickSlider {...RELATED_NEWS_SLIDER_SETTINGS}>
				{this.props.data.map(relatedNewsSliderPage)}
			</SlickSlider>
		);
	}
});


