import React from 'react'
import ReactDOM from 'react-dom';

import Navigation from '../../components/Navigation'
import ImageTitle from '../../components/ImageTitle'
import Breadcrumbs from '../../components/Breadcrumbs'
import RelatedNews from '../../components/RelatedNews'
import Footer from '../../components/Footer'

import './newsroom-item.scss'

import 'whatwg-fetch'

export default class extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			newsItem: {title: 'Loading..'},
			relatedNews: []
		}
	}
	findNewsById(id, news) {
		return news.filter(item => item.id == id)[0]
	}
	componentDidMount() {
		this.fetchNewsView(this.props.params.newsId)
	}
	componentWillReceiveProps(nextProps) {
	    var nextNewsId = nextProps.params.newsId;
	    if (nextNewsId !== this.props.params.newsId) {
	    	this.fetchNewsView(nextNewsId);
	    }
	}
	fetchNewsView(nextNewsId) {
		this.setState({
			newsItem: {title: 'Loading..'},
			relatedNews: []
		})

		fetch('/news.json?id=' + nextNewsId)
			.then(response => response.json())
			.then(news => 
				this.setState({
					newsItem: this.findNewsById(nextNewsId, news) || this.state.newsItem,
					relatedNews: news
				})
			)
	}
  	render() {
  		let newsItem = this.state.newsItem,
  			breadcrumbsItems = [
  				{title: 'Athenaeg', url: '/'},
  				{title: 'Newsroom', url: '/newsroom'},
  				{title: newsItem.title}
  			]
	    return (
	    	<div className="app">
				<Navigation white ancillary/>

				<ImageTitle ancillary image="/img/newsroom.jpg">Newsroom</ImageTitle>
				<div className="newsroom-content">
					<div className="wrapper-1000px">
						<Breadcrumbs items={breadcrumbsItems}/>
						<h2 className="middle-title">{newsItem.title}</h2>
						<div className="middle-subtitle middle-subtitle_light">{newsItem.date}</div>
						<img src={newsItem.image} />
					</div>
					<div className="wrapper-1000px">
						<p className="gray-text">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Donec enim enim, posuere eu efficitur ut, convallis consectetur orci. Curabitur suscipit at tortor in maximus. Curabitur vitae rhoncus nibh. Vestibulum viverra, nisl elementum eleifend blandit, lorem augue sagittis dui, ut varius velit odio eget dolor. Maecenas posuere, ipsum ac euismod sagittis, mauris massa ornare massa, eu pulvinar lacus sem eget lorem. Morbi ac turpis a velit blandit malesuada eget ut lacus. Nullam mattis nec nunc nec porttitor. Pellentesque nec pulvinar magna.</p>
						<p className="gray-text">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Donec enim enim, posuere eu efficitur ut, convallis consectetur orci. Curabitur suscipit at tortor in maximus. Curabitur vitae rhoncus nibh. Vestibulum viverra, nisl elementum eleifend blandit, lorem augue sagittis dui, ut varius velit odio eget dolor. Maecenas posuere, ipsum ac euismod sagittis, mauris massa ornare massa, eu pulvinar lacus sem eget lorem. Morbi ac turpis a velit blandit malesuada eget ut lacus. Nullam mattis nec nunc nec porttitor. Pellentesque nec pulvinar magna.</p>
						<p className="gray-text">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Donec enim enim, posuere eu efficitur ut, convallis consectetur orci. Curabitur suscipit at tortor in maximus. Curabitur vitae rhoncus nibh. Vestibulum viverra, nisl elementum eleifend blandit, lorem augue sagittis dui, ut varius velit odio eget dolor. Maecenas posuere, ipsum ac euismod sagittis, mauris massa ornare massa, eu pulvinar lacus sem eget lorem. Morbi ac turpis a velit blandit malesuada eget ut lacus. Nullam mattis nec nunc nec porttitor. Pellentesque nec pulvinar magna.</p>
						<p className="gray-text">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Donec enim enim, posuere eu efficitur ut, convallis consectetur orci. Curabitur suscipit at tortor in maximus. Curabitur vitae rhoncus nibh. Vestibulum viverra, nisl elementum eleifend blandit, lorem augue sagittis dui, ut varius velit odio eget dolor. Maecenas posuere, ipsum ac euismod sagittis, mauris massa ornare massa, eu pulvinar lacus sem eget lorem. Morbi ac turpis a velit blandit malesuada eget ut lacus. Nullam mattis nec nunc nec porttitor. Pellentesque nec pulvinar magna.</p>
					</div>
				</div>
				<hr/>
				<RelatedNews data={this.state.relatedNews}/>
				<Footer gray />
			</div>
	    )
  	}
}




