import React from 'react'
import locale from 'locale'
import SlickSlider from 'react-slick';
import {LeftSliderButton, RightSliderButton} from '../SliderArrows/'
import './endorsements.scss'

export default class Endorsements extends React.Component {
	render() {
		return (
			<div className="endorsements">
				<h3 className="slider__title">{locale.str('endorsements.title')}</h3>
				<div className="wrapper-1000px">
					<EndorsementsSlider data={this.props.endorsements}/>
				</div>
			</div>
		)
	}
}

const EndorsementsSlider = React.createClass({
	render() {
      let slides = this.props.data

      let isScrollsDesktop = slides.length > 3,
          isScrollsMobile = slides.length > 1

    	let settings = {
      		dots: isScrollsDesktop,
      		infinite: isScrollsDesktop,
      		speed: 1000,
            arrows: isScrollsDesktop,
      		autoplay: isScrollsDesktop,
      		autoplaySpeed: 10000,
      		slidesToShow: 3,
      		slidesToScroll: 3,
      		pauseOnHover: true,
      		nextArrow: <RightSliderButton/>,
      		prevArrow: <LeftSliderButton/>,
      		className: 'endorsements-slider',
      		dotsClass: 'slider__dots',
            responsive: [
                {
                    breakpoint: 768,
                    settings: {
                        dots: isScrollsMobile,
                        speed: 300,
                        autoplay: false,
                        arrows: false,
                        slidesToShow: 1,
                        slidesToScroll: 1,
                    }
                }
            ]
    	};
    	return (
      		<SlickSlider {...settings}>
        		{slides.map(endorsementsSliderPage)}
     		</SlickSlider>
    	);
  	}
});

// Returns markup for slider page in Endorsements section
// @image - photo of a man
// @name - name of a man
// @company - subtitle below the name
// @description - text of the endorsement. Will be quoted
function endorsementsSliderPage(item, index) {
	let companyChunks = item.company.split('\n').map((company, index) => <span key={index}>{company}<br/></span>);

	return (
		<div key={index} className="endorsements-item">
			<img className="endorsements-item__image"src={item.image}/>
			<div className="endorsements-item__title">{item.name}</div>
			<div className="endorsements-item__subtitle">{companyChunks}</div>
			<div className="endorsements-item__description">“{item.description}”</div>
 		</div>
 	)
}


