	'about.first_paragraph': 'The first annual National Youth Orchestra of China is proud to present an international concert tour of the United States and China during the summer of 2017, inaugurated by a gala performance in Carnegie Hall. For two weeks in July, approximately one hundred high school-aged musicians will gather on a university campus in the Northeast United States to participate in an intensive two-week training program under the direction of a group of world-class teaching artists and a renowned guest conductor, Ludovic Morlot. After two weeks of preparation, the orchestra will debut in New York City and thereafter embark on a three-city concert tour of China featuring a soloist who will resonate with Chinese and Western audiences alike.',
