	'training_residency.description': 'NYO-China’s official training residency is designed to allow members to develop as musicians under the instruction of the finest teaching artists in the world. For two weeks, students will gather at Western Connecticut State University (WCSU), located just outside of New York City, where they will take part in a regimen of musical and extracurricular activities meant to both hone their individual musical skills and prepare them as an orchestra. During the day, members will participate in a variety of musical activities – full orchestra rehearsals under NYO-China’s music director and guest conductor, sectionals and master classes led by its teaching artists, and other relevant seminars and workshops described below. Beyond these more formal activities, students will also have the opportunity to participate in cultural excursions in the New York City area and to take part in social interactions with Carnegie Hall’s NYO-USA.',
