import React from 'react'
import ReactDOM from 'react-dom';

import Navigation from '../../components/Navigation'
import ImageTitle from '../../components/ImageTitle'
import NewsroomCard from '../../components/NewsroomCard'
import Footer from '../../components/Footer'

import './newsroom.scss'

import 'whatwg-fetch'

export default class extends React.Component {
	constructor(props) {
		super(props)

		this.state = {news: [], page: 1}

		this.loadMore = this.loadMore.bind(this)
	}
	componentDidMount() {
		// get news array
		fetch('news.json')
			.then(response => response.json())
			.then(news => this.setState({news: news}))
	}
	loadMore() {
		let currentNews = this.state.news
		let page = this.state.page + 1

		fetch('news.json?page=' + page)
			.then(response => response.json())
			.then(nextNews => {
				let _debugArray = [];
				let _lastNewsId = currentNews[currentNews.length - 1].id
				_debugArray = nextNews.map(item => {
					let updatedItem = item
					updatedItem.id = ++_lastNewsId
					return updatedItem
				})
				this.setState({
					news: currentNews.concat(_debugArray),
					page
				})
			})
	}
  	render() {
  		let news = this.state.news,
  			cards = news.map((news, index) => {
  			return <NewsroomCard
  						id={news.id}
						image={news.preview}
						title={news.title}
						subtitle={news.subtitle}
						date={news.date}
						key={news.id}/>
  		})
	    return (
	    	<div className="app">
				<Navigation white ancillary/>
				<ImageTitle ancillary image="img/newsroom.jpg">Newsroom</ImageTitle>
				<div className="wrapper-1000px wrapper-padding-top wrapper-padding-bottom">
					<div className="newsroom-cards">{cards}</div>
					<button className="newsroom__load-more" onClick={this.loadMore}>Load more</button>
				</div>
				<Footer gray/>
			</div>
	    )
  	}
}


