import React from 'react'
import { Link } from 'react-router'
import './newsroom-card.scss'

/*
	News preview card
	@id - news id
	@image - news image preview (square, min - 300x300px)
	@title - news headline
	@subtitle - news subtitle
	@date - news date
*/
export default class extends React.Component {
	render() {
		return (
			<Link to={`/newsroom/${this.props.id}`} className="newsroom-card">
				<div className="newsroom-card__thumb">
					<img className="newsroom-card__image" src={this.props.image}/>
				</div>
				<div className="newsroom-card__title">{this.props.title}</div>
				<div className="newsroom-card__date">{this.props.date}</div>
			</Link>
		)
	}
}


