import React from 'react'

export class LeftSliderButton extends React.Component {
  	render() {
    	return <button {...this.props}><i className="icon icon-gallery-left"></i></button>  
  	}
}

export class RightSliderButton extends React.Component {
  	render() {
    	return <button {...this.props}><i className="icon icon-gallery-right"></i></button>  
  	}
}


