import React from 'react'
import locale from 'locale'
import './hero.scss'

export default class extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			padding: 0,
			height: document.body.clientHeight
		}

		this.updatePadding = this.updatePadding.bind(this)
		this.calculatePadding = this.calculatePadding.bind(this)
		this.orientationChanged = this.orientationChanged.bind(this)
	}
	componentDidMount() {
		window.addEventListener('webkitfullscreenchange', this.orientationChanged)
		window.addEventListener('mozfullscreenchange', this.orientationChanged)
		window.addEventListener('fullscreenchange', this.orientationChanged)
		window.addEventListener('MSFullscreenChange', this.orientationChanged)
		window.addEventListener('webkitorientationchange', this.orientationChanged)
		window.addEventListener('mozorientationchange', this.orientationChanged)
		window.addEventListener('orientationchange', this.orientationChanged)
		window.addEventListener('resize', this.updatePadding)
		this.updatePadding();
	}
	componentWillUnmount() {
		window.removeEventListener('webkitfullscreenchange', this.orientationChanged)
		window.removeEventListener('mozfullscreenchange', this.orientationChanged)
		window.removeEventListener('fullscreenchange', this.orientationChanged)
		window.removeEventListener('MSFullscreenChange', this.orientationChanged)
		window.removeEventListener('orientationchange', this.orientationChanged)
		window.removeEventListener('resize', this.updatePadding)
	}
	updatePadding() {
		let padding = this.calculatePadding(),
			height = document.body.clientHeight

		this.setState({padding, height})
	}
	// special case for fullscreen change
	orientationChanged() {
		setTimeout(this.updatePadding(), 1000)
	}
	calculatePadding() {
		let contentHeight = this.refs.content.getBoundingClientRect().height,
			blockHeight = this.refs.block.getBoundingClientRect().height,
			applyHeight = this.refs.apply.getBoundingClientRect().height;
		return (blockHeight / 2 - contentHeight / 2 - applyHeight / 2) / 2
	}

	render() {
		let applyStyle = {
			padding: this.state.padding + 'px 0'
		}
		let heroStyle = {
			height: this.state.height + 'px'
		}

		return (
			<div className="hero" style={heroStyle}>
				{/*
				<video className="hero__video" autoPlay loop preload>
					<source src="video/hero.mp4" type="video/mp4"/>
					<source src="video/hero.webm" type="video/webm"/>
				</video>
				*/}
				<div className="hero__middle-container" ref="block">
					<div className="hero__content" ref="content">
						<div className="hero__title hero__title_present">{locale.str('hero.first_row')}</div>
						<div className="hero__title hero__title_event">{locale.str('hero.second_row')}</div>
						<div className="hero__title hero__title_where">{locale.str('hero.third_row')}</div>
					</div>
				</div>
				<div className="hero__button-container" style={applyStyle}>
					<button className="button button_apply  fade-tooltip disabled" ref="apply" data-title="Applications Not Yet Open">{locale.str('common.buttons.apply')}</button>
				</div>
			</div>
		)
	}
}


