import React from 'react'
import cx from 'classnames'
import './contact-icons.scss'

export class ContactIcons extends React.Component {
	render() {
		let className = cx('contact-us__icon-blocks', {'contact-us__icon-blocks_white': this.props.white})
		return <div className={className}>{this.props.children}</div>
	}
}

export class ContactIcon extends React.Component {
	render() {
		let iconClassName = cx('icon', 'contact-us-icon-block__icon', 'icon-' + this.props.icon)
		return (
			<div className="contact-us-icon-block">
				<i className={iconClassName}></i>
				<div className="contact-us-icon-block__text">{this.props.children}</div>
			</div>
		)
	}
}


