import React from 'react'
import cx from 'classnames'

import './intro-card.scss'

export default class IntroCard extends React.Component {
	render() {
		let { align, image, title, subtitle, description } = this.props

		let imageClassName = cx('intro-card__image', `intro-card__image_${align}`),
			textClassName = cx('intro-card__text', `intro-card__text_${align}`),
			link = '#' + (title.trim().toLowerCase().replace(' ', '_'))

		return (
			<div className="intro-card">
				<img className={imageClassName} src={image}/>
				<div className={textClassName}>
					<div className="intro-card__content">
						<a className="intro-card__title" data-scroll href={link}>{title}</a>
						<img className={imageClassName} src={image}/>
					</div>
					<div className="intro-card__subtitle">{subtitle}</div>
					<div className="intro-card__description"><p className="gray-text">{description}</p></div>
				</div>
			</div>
		)
	}
}

IntroCard.propTypes = { align: React.PropTypes.oneOf(['left', 'right']) }
IntroCard.defaultProps = { align: 'left' }


