let locales = {
	en: require('./en'),
	ch: require('./ch')
}

function setLocale(locale) {
	localStorage.setItem('alpha2', locale)
}

function getLocale() {
	return localStorage.getItem('alpha2') || 'en'
}

function getLocalizedString(string) {
	let alpha2 = getLocale()
	return locales[alpha2][string] || string
}

export default {
	setLocale,
	getLocale,
	str: getLocalizedString
}


