import React from 'react'

import Navigation from '../../components/Navigation'
import Hero from '../../components/Hero'
import IntroSliders from '../../components/IntroSliders'
import ImageTitle from '../../components/ImageTitle'
import About from '../../components/About/'
import Artists from '../../components/Artists/'
import TrainingResidency from '../../components/TrainingResidency/'
import ConcertTour from '../../components/ConcertTour/'
import Auditions from '../../components/Auditions/'
import Contact from '../../components/Contact'
import Footer from '../../components/Footer'
import locale from 'locale'

export default class extends React.Component {
  	render() {
		const { data } = this.props;
	    return (
	    	<div className="app">
	    		<Navigation />
	    		<Hero/>

				<IntroSliders
					endorsements={data.endorsements}
					sponsors={data.sponsors}/>

				<ImageTitle image="img/about.jpg">{locale.str('common.about')}</ImageTitle>
				<About
					aboutData={data.about_data}
					auditionProcessData={data.audition_process_data}
					auditionProcessDuration={data.audition_process_duration}
					trainingResidencyDuration={data.training_residency_duration}
					concertTourDuration={data.concert_tour_duration}
					concertTourData={data.concert_tour_data}/>

				<ImageTitle image="img/artists.jpg">{locale.str('common.artists')}</ImageTitle>
				<Artists artists={data.headlining_artists}/>

				<ImageTitle image="img/training-residency.jpg">{locale.str('common.training_residency')}</ImageTitle>
				<TrainingResidency
					images={data.w_conservatory.image_grid}
					faculty={data.w_conservatory.faculty}
					auditions={data.auditions}/>

				<ImageTitle image="img/concert-tours.jpg">{locale.str('common.concert_tour')}</ImageTitle>
				<ConcertTour data={data.concert_tour_data}/>

				<ImageTitle image="img/auditions.jpg">{locale.str('common.auditions')}</ImageTitle>
				<Auditions auditionsRequirements={data.auditions} auditionsTimeline={data.auditions_timeline}/>

				<Contact/>

				<Footer/>
			</div>
	    )
  	}
}


