import React from 'react'
import cx from 'classnames'

import './image-title.scss'

// Block with image on background
// required prop @image - path to image
// children must me a string
export default class extends React.Component {
	shouldComponentUpdate() {
		return false
	}
	render() {
		var className = cx('image-title', {'image-title_ancillary': this.props.ancillary})
		var styles = {
				backgroundImage: 'url(' + this.props.image + ')'
			},
			// generate id for the block
			// by converting children of component to valid DOM id string
			id = !this.props.ancillary ? this.props.children.toString().trim().toLowerCase().replace(' ', '_') : '';
			var imageTitle = this.props.ancillary ? <h1 className="image-title__text">{this.props.children}</h1> : <div className="image-title__text">{this.props.children}</div>;
		return (
			<div className={className} style={styles} id={id}>
				{imageTitle}
			</div>
		)
	}
}


