import React, { Component, PropTypes } from 'react'
import cx from 'classnames'

import './artist-card.scss'

export default class ArtistCard extends React.Component {
	render() {
		let { align, description, image, title, subtitle } = this.props

		let imageClassName = cx('artist-card__image', `artist-card__image_${align}`),
			textClassName = cx('artist-card__text', `artist-card__text_${align}`)

		return (
			<div className="artist-card">
				<img className={imageClassName} src={image}/>
				<div className={textClassName}>
					<div className="artist-card-titles">
						<div className="artist-card__title">{title}</div>
						<div className="artist-card__subtitle">{subtitle}</div>
					</div>
					<div className="artist-card__description">
						<p className="gray-text">{description}</p>
					</div>
				</div>
			</div> 
		)
	}
}

ArtistCard.propTypes = {
	align: PropTypes.oneOf(['left', 'right']),
	title: PropTypes.string,
	subtitle: PropTypes.string,
	image: PropTypes.string,
	description: PropTypes.string
}
ArtistCard.defaultProps = {
	align: 'left'
}


