import React, { Component } from 'react'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'
import cx from 'classnames'

import './timeline-item.scss'
import './tooltip.scss'

export class TimelineItem extends Component {
	render() {
		let { align, icon, title, date, children, isTooltipOpen, onItemIn, onItemOut, onItemClick } = this.props

		let classes = cx('timeline__block', {'timeline__block_active': isTooltipOpen});
  		if (align) {
  			classes += ' timeline__block_' + align
  		}
  		let fontIconClasses = 'icon icon-'+ icon;
  		let iconClasses = cx('timeline-item__icon', {'timeline-item__icon_active': isTooltipOpen});

  		let tooltip = null

  		if (document.body.offsetWidth <= 768) {
  			tooltip = isTooltipOpen ? children : null
  		} else {
  			tooltip = (
  				<ReactCSSTransitionGroup transitionName="tooltip-animation" transitionEnterTimeout={150} transitionLeaveTimeout={150}>
					{isTooltipOpen && children}
				</ReactCSSTransitionGroup>
  			)
  		}

		return (
			<div className={classes}>
				<div className="timeline-item">
					<div className="timeline-item__event">{title}</div>
					<div className={iconClasses} onMouseEnter={onItemIn} onMouseLeave={onItemOut} onClick={onItemClick}>
				  		<i className={fontIconClasses}></i>
					</div>
					<div className="timeline-item__date">{this.props.date}</div>
					{tooltip}
				</div>
		  	</div>
		)
	}
}

export class Tooltip extends Component {
	componentDidMount() {
		let { align } = this.props
		let { tooltip, arrow } = this.refs

		if (align) {
			let parentWidth = tooltip.parentNode.parentNode.clientWidth,
				arrowOffset = (parentWidth / 2 - 10) + 'px'; // 10 is arrow width

			switch (align) {
				case 'left':
					arrow.style.right = 'auto';
					arrow.style.left = arrowOffset;
					break;
				case 'right':
					arrow.style.left = 'auto';
					arrow.style.right = arrowOffset;
					break;
			}
		}
	}

	render() {
		let classes = 'tooltip' + (this.props.align ? ' tooltip_' + this.props.align : '');
		return (
			 <div className={classes} ref="tooltip">
			 	<span className="tooltip__arrow" ref="arrow"></span>
			 	<div className="tooltip__content">{this.props.children}</div>
			 </div>
		)
	}
}


