import React from 'react'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'
import SlickSlider from 'react-slick';

import locale from 'locale'
import TheMap from '../TheMap/'
import { LeftSliderButton, RightSliderButton } from '../SliderArrows/'

import './concert-tour.scss'

const LOCATION_PHOTOS_SLIDER_SETTINGS = {
	dots: true,
	infinite: true,
	speed: 500,
	slidesToShow: 1,
	slidesToScroll: 1,
	nextArrow: <RightSliderButton/>,
	prevArrow: <LeftSliderButton/>,
	className: 'concert-tour-slider',
	dotsClass: 'slider__dots',
	responsive: [
		{
			breakpoint: 768,
			settings: {
				arrows: false
			}
		}
	]
}

class ConcertTourScheduleItem extends React.Component {
	render() {
		let event = this.props.event;
		return (
			<div className="schedule-item">
				<div className="schedule-item-info">
					<div className="schedule-item-info__date">{event.date}</div>
					<div className="schedule-item-info__city">{event.city}</div>
					<div className="schedule-item-info__name">{event.name}</div>
					<div className="schedule-item-info__role">{event.soloist}</div>
				</div>
				<div className="schedule-item__calendar-block">
					<a href={event.ics} download className="schedule-item__calendar"><i className="icon icon-calendar"></i></a>
				</div>
				<div className="schedule-item__get-ticket-block">
					<a href="javascript:void(0)" className="button button_get-tickets fade-tooltip disabled" data-title="Tickets Not Yet Available">Get Tickets</a>
				</div>
			</div>
		)
	}
}


class ConcertTourSchedule extends React.Component {
	render() {
		let { events } = this.props

		return (
			<div className="schedule">
				{ events.map((event, index) => <ConcertTourScheduleItem key={index} event={event}/>) }
			</div>
		)
	}
}


export default class ConcertTour extends React.Component {
	state = {
		locationIndex: -1
	}

	handleLocationClick(index) {
		this.setState({
			locationIndex: this.state.locationIndex === index ? -1 : index
		}, function() {
			// fix for image resize
			if (this.state.locationIndex !== -1) {
				setTimeout(() => {
					this.setState({locationIndex: this.state.locationIndex})
				}, 250)
			}
		})
	}

	handleCloseInfo() {
		this.setState({ locationIndex: -1 })
	}

	render() {
		let concertTourLocationPopup = null;

		// shop popup
		if (this.state.locationIndex !== -1) {
			let concertTourLocationPhotos = this.props.data[this.state.locationIndex].photos;
			let locationItem = function(photo, index) {
				return (
					<div className="concert-tour-location-info__image" key={index}>
						<img src={photo}/>
					</div>
				)
			}

			const ConcertTourSlider = React.createClass({
				render() {
					return (
						<SlickSlider {...LOCATION_PHOTOS_SLIDER_SETTINGS}>
							{concertTourLocationPhotos.map(locationItem)}
						</SlickSlider>
					);
				}
			});
			
			concertTourLocationPopup = (
				<div className="concert-tour-location-info">
					<div className="concert-tour-location-info__close" onClick={this.handleCloseInfo}>
						<i className="icon icon-close"></i>
					</div>
					<ConcertTourSlider/>
					<div className="concert-tour-location-info__content">
						<p className="gray-text">{this.props.data[this.state.locationIndex].place_description}</p>
					</div>
				</div>
			)
		}

		let locationsSlides = this.props.data.map((item, index) => {
			return (
				<div key={index} className="concert-tour-location" onClick={this.handleLocationClick.bind(this, index)}>
					<div className="concert-tour-location__container">
						<div className="concert-tour-location__image" style={{backgroundImage: 'url(' + item.photos[0] + ')'}}></div>
						<div className="concert-tour-location__place">{item.place}</div>
						<div className="concert-tour-location__city">{ [item.city, item.country].join(', ') }</div>
					</div>
				</div>
			)
		})

		return (
			<div className="concert-tour">
				<div className="wrapper-1000px wrapper-padding-top">
					{/* TODO: uncomment venues and remove margin later */}
					<p className="gray-text" style={{marginBottom: 90}}>{locale.str('concert_tour.description')}</p>
					{/*<div className="concert-tour__locations-block">
						<div className="concert-tour__locations">
							{locationsSlides}
						</div>
						{concertTourLocationPopup !== null && <div className="opacity" onClick={::this.handleCloseInfo}></div>}
						<ReactCSSTransitionGroup transitionName="concert-tour-info-animation" transitionEnterTimeout={250} transitionLeaveTimeout={150}>
							{concertTourLocationPopup}
						</ReactCSSTransitionGroup>
					</div>*/}
					<TheMap points={this.props.data}/>
					<ConcertTourSchedule events={this.props.data}/>
				</div>
			</div>
		)
	}
}


