import React, { Component, PropTypes } from 'react'
import locale from 'locale'

// import Video from '../Video'
import IntroCard from '../IntroCard'
import Timeline from '../Timeline'
import TuitionFree from '../TuitionFree'

import './about.scss'

class About extends Component {
	introCardMarkup(item, index) {
		return (
			<IntroCard
				key={index}
				image={item.image}
				title={item.title}
				description={item.description}
				align={item.align}/>
		)
	}
	render() {
		let {
			aboutData,
			trainingResidencyDuration,
			auditionProcessDuration,
			concertTourDuration,
			auditionProcessData,
			concertTourData
		} = this.props

		return (
			<div className="about__content">
				<div className="wrapper-1000px wrapper-padding-top wrapper-padding-bottom">
					<p className="gray-text gray-text_bold">{locale.str('about.mission')}</p>
					<p className="gray-text">{locale.str('about.first_paragraph')}<br/><br/>{locale.str('about.second_paragraph')}
					</p>
					{/*
					<div className="wrapper-padding-top">
						<Video title="The Imperial March by the Vienna Philharmonic Orchestra">
							<source src="video/vienna.mp4" type="video/mp4"/>
						</Video>
					</div>
					*/}
				</div>
				<hr/>		 	
				<div className="wrapper-1000px">
					<div className="wrapper-padding-top wrapper-padding-bottom">{ aboutData.map(this.introCardMarkup) }</div>
					<hr className="short"/>
					<Timeline
						trainingResidencyDuration={trainingResidencyDuration}
						auditionProcessDuration={auditionProcessDuration}
						concertTourDuration={concertTourDuration}
						auditionProcessData={auditionProcessData}
						concertTourData={concertTourData}/>
				</div>
				<hr/>
				<div className="wrapper-1000px wrapper-padding-top wrapper-padding-bottom">
					<TuitionFree/>
				</div>
			</div>
		)
	}
}

About.propTypes = {
	trainingResidencyDuration: PropTypes.string.isRequired,
	auditionProcessDuration: PropTypes.string.isRequired,
	concertTourDuration: PropTypes.string.isRequired,
	auditionProcessData: PropTypes.array.isRequired,
	concertTourData: PropTypes.array.isRequired,
	aboutData: PropTypes.array.isRequired
}

export default About


